@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&family=Inter&family=Rajdhani&family=Raleway&family=Rubik:wght@800&family=Sacramento&family=Space+Grotesk&display=swap");

:root {
  --primary-colour: #f8f9fa;
  --text-colour: #343a40;

  --font-1: "Sacramento", cursive;
  --font-2: "Space Grotesk", sans-serif;
  --font-3: "Raleway", sans-serif;
  --font-4: "Rubik", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  font-family: var(--font-2);
}

.project-2{
    display: flex;
  
    align-items: center;

    overflow: hidden;
  }
  
  .p2-img-c1{
    transform: rotate(-15deg);
    width: 250px;
    height: 400px;
  }
  .p2-img-c2{
    transform: rotate(-15deg);
    width: 250px;
    height: 900px;
  
    display: flex;
    flex-direction: column;
  
    justify-content: start;
  }
  .p2-img-c3{
    margin-left: 40px;
    transform: rotate(-15deg);
    width: 250px;
    height: 400px;
  }
  
  .p-2-img-1{
    width: 100%;
    height: 100%;
  
    background-image: url("../../../assets/chatAPP/c1.png");
    background-repeat: no-repeat;
    background-position:start;
    background-size:contain;
  }
  .p-2-img-2{
    width: 100%;
    height: 50%;
  
    background-image: url("../../../assets/chatAPP/c2.png");
    background-repeat: no-repeat;
    background-position:start;
    background-size:contain;
  }
  .p-2-img-3{
    width: 100%;
    height: 50%;
  
    background-image: url("../../../assets/chatAPP/c3.png");
    background-repeat: no-repeat;
    background-position:start;
    background-size:contain;
  }
  .p-2-img-4{
    width: 100%;
    height: 100%;
  
    background-image: url("../../../assets/chatAPP/c4.png");
    background-repeat: no-repeat;
    background-position:start;
    background-size:contain;
  }
  
  .p2-details{
    flex: 1;
    /* background-color:aqua; */
  
    height: 400px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  
    padding: 0 40px;
  }
  
  .p2-name-source{
    display: flex;
  }
  
  .p2-name{
    font-family: var(--font-2);
    font-weight: 900;
  
    font-size: 64px;
  }
  
  .p2-source-code{
    width: 25px;
    height: 25px;
    margin-left: 10px;
    margin-top: 12px;
  
    background-image: url("/src/assets/icons/external-link.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  
    transition: background-image 0.3s ease, transform 0.3s ease;
  }
  
  .p2-source-code:hover{
    background-image: url("/src/assets/icons/external-link2.png");
    transform: scale(1.5);
  }
  
  .p2-discription{
    margin-top: 10px;
    width: 100%;
    /* line-height: 20px;
    height: calc(2*20px);
    overflow: hidden;
    position: relative; */
  
    word-spacing: 5px;
    text-align: justify;
  }
  
  /* .p2-discription::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    height: 20px;
    width: 100%;
    background: linear-gradient(90deg, transparent, var(--primary-colour));
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
  }
  
  .p2-discription:hover::after {
    opacity: 0;
  }
  
  .p2-discription:hover {
    height: auto;
    transition: height 0.3s ease-in-out;
    overflow: visible;
  } */
  
  .p2-tools-used{
    margin-top: 20px;
    width: 100%;
  
    display: flex;
    justify-content: start;
  
    /* background-color: #343a40; */
  }
  
  #p2-tool{
    height: 50px;
    width: 50px;
  
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  
  .p2-tool-1{
    background-image: url("/src/assets/dart.png");
  }
  .p2-tool-2{
    background-image: url("/src/assets/flutter.png");
  }
  .p2-tool-3{
    background-image: url("/src/assets/firebase.png");
  }
  .project-img-p2{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media only screen and (max-width: 770px){
    .project-2{
      margin-top: 100px;
      flex-direction: column;
    }
    .p2-img-c1{
      width: 100px;
      height: 250px;
    }
    .p2-img-c2{

      width: 100px;
      height: 400px;
    }
    .p2-img-c3{
      width: 100px;
      height: 250px;
      margin-left: 0px;
    }
    .project-img-p2{
      order: 1;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .p2-name{
      font-size: 32px;
    }
    .p2-source-code{
      width: 15px;
      height: 15px;
    }
    .p2-source-code:hover{
      transform: scale(1.2);
    }
    .p2-discription{
      /* line-height: 18px;
      height: calc(2*18px); */
      word-spacing: 0px;
      font-size: 14px;
    }
    #p2-tool{
      height: 30px;
      width: 30px;
    }
    .p2-details{
      order: 2;
    }
    .p2-tools-used{
      justify-content: end;
    }
  }