@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&family=Inter&family=Rajdhani&family=Raleway&family=Rubik:wght@800&family=Sacramento&family=Space+Grotesk&display=swap");

:root {
  --primary-colour: #f8f9fa;
  --text-colour: #343a40;

  --font-1: "Sacramento", cursive;
  --font-2: "Space Grotesk", sans-serif;
  --font-3: "Raleway", sans-serif;
  --font-4: "Rubik", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  font-family: var(--font-2);
}

.project-1 {
  display: flex;

  align-items: center;
}

.p1-img-c1 {
  transform: rotate(15deg);
  width: 250px;
  height: 400px;
}
.p1-img-c2 {
  transform: rotate(15deg);
  width: 250px;
  height: 900px;

  display: flex;
  flex-direction: column;

  justify-content: start;
}
.p1-img-c3 {
  margin-left: 40px;
  transform: rotate(15deg);
  width: 250px;
  height: 400px;
}

.p-1-img-1 {
  width: 100%;
  height: 100%;

  background-image: url("../../../assets/EDUZU/e1.png");
  background-repeat: no-repeat;
  background-position: start;
  background-size: contain;
}
.p-1-img-2 {
  width: 100%;
  height: 50%;

  background-image: url("../../../assets/EDUZU/e2.png");
  background-repeat: no-repeat;
  background-position: start;
  background-size: contain;
}
.p-1-img-3 {
  width: 100%;
  height: 50%;

  background-image: url("../../../assets/EDUZU/e3.png");
  background-repeat: no-repeat;
  background-position: start;
  background-size: contain;
}
.p-1-img-4 {
  width: 100%;
  height: 100%;

  background-image: url("../../../assets/EDUZU/e4.png");
  background-repeat: no-repeat;
  background-position: start;
  background-size: contain;
}

.p1-details {
  flex: 1;
  /* background-color:aqua; */

  height: 900px;
  display: flex;
  justify-content: center;
  flex-direction: column;

  padding: 0 40px;
}

.p1-name-source {
  display: flex;
}

.p1-name {
  font-family: var(--font-2);
  font-weight: 900;

  font-size: 64px;
}

.p1-source-code {
  width: 25px;
  height: 25px;
  margin-left: 10px;
  margin-top: 12px;

  background-image: url("/src/assets/icons/external-link.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  transition: background-image 0.3s ease, transform 0.3s ease;
}

.p1-source-code:hover {
  background-image: url("/src/assets/icons/external-link2.png");
  transform: scale(1.5);
}

.p1-discription {
  margin-top: 10px;
  width: 100%;
  /* line-height: 20px; */
  /* height: calc(2*20px); */
  /* overflow: hidden; */
  /* position: relative; */

  word-spacing: 5px;
  text-align: justify;
}

