@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&family=Inter&family=Rajdhani&family=Raleway&family=Rubik:wght@800&family=Sacramento&family=Space+Grotesk&display=swap");

:root {
  --primary-colour: #f8f9fa;
  --text-colour: #343a40;

  --font-1: "Sacramento", cursive;
  --font-2: "Space Grotesk", sans-serif;
  --font-3: "Raleway", sans-serif;
  --font-4: "Rubik", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  font-family: var(--font-2);
}

.contact-main{
    width: 100vw;
    /* height: 100%; */

    display: flex;
    background-color: var(--text-colour);
    padding-bottom: 50px;
    padding-top: 20px;
}

.contact-flex-div1{
    margin: 5vw 0;
    width: 100vw;  /* when i am showing the contact below */
    /* width: 50vw; */
    /* height: 100%; */
    /* background-color: aqua; */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact-flex-div2{
    margin: 5vw 0;
    width: 50vw;
    /* height: 100%; */

    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-logo{
    /* background-color: aliceblue; */

    width: 30vw;
    height: 200px;

    /* background-image: url("../../assets/Piyush\'s\ Logo.png"); */
    background-image: url("../../assets/Logo/PS_LOGO.png");
    background-repeat: no-repeat;
    background-position:center;
    background-size:contain;
    filter: drop-shadow(0px 0px 10px rgba(0,0,0,0.4));
}

.download{
    margin-top: 50px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50px;
    border: 1px solid var(--primary-colour);

    padding: 10px 20px;

    /* color: var(--primary-colour); */
} 

.download:hover {
    font-weight: 600;
    border: 2px solid var(--primary-colour);
}

.download-logo{
    margin-left: 10px;

    width: 20px;
    height: 20px;

    background-image: url("../../assets/download.png");
    background-repeat: no-repeat;
    background-position:start;
    background-size:contain;
}

.download-title{
    color: var(--primary-colour);
    text-decoration: underline;
    text-decoration-color: var(--primary-colour);
}

.contact-form{
    display: flex;

    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: var(--primary-colour);
}

#form-div{
    display: flex;

    flex-direction: column;

    width: 30vw;

    margin-top: 30px;
}

.form-input-1{
    height: 30px;
    padding-left: 10px;

    border-radius: 5px;
    border: none;
}
.form-input-2{
    height: 30px;
    padding-left: 10px;

    border-radius: 5px;
    border: none;
}
.form-input-3{
    height: 100px;
    padding-top: 10px;
    padding-left: 10px;

    border-radius: 5px;
    border: none;
}
.form-btn-div{
    margin-top: 20px;

    width: 30vw;

    display: flex;
    justify-content: end;
}
.form-btn{
    padding: 8px 20px;
    border-radius: 5px;
    border: none;
}
.form-btn:hover{
    font-weight: 600;
}
.contact-SocialMedia{
    display: flex;
    margin-top: 50px;
    justify-content: space-between;
    width: 20vw;
  }
  .contact-Mail{
    background-image: url("/src/assets/icons8-mail-96.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  
    width: 22px;
    height: 22px;
  
    transition: background-image 0.3s ease, transform 0.3s ease;
  }
  
  .contact-GitHub{
    background-image: url("/src/assets/icons8-github-90.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  
    width: 25px;
    height: 25px;
    transition: background-image 0.3s ease, transform 0.3s ease;
  }
  
  .contact-Linkedin{
    background-image: url("/src/assets/icons8-linkedin-90.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  
    width: 22px;
    height: 22px;
    
    transition: background-image 0.3s ease, transform 0.3s ease;
  }

  #SM:hover{
    scale: 1.3;
    filter: drop-shadow(0px 0px 10px rgba(0,0,0,1))
  }

@media only screen and (max-width: 770px) {
    .contact-main{
        flex-direction: column;
    }
    .contact-flex-div1{
        width: 100%;
        height: 50%;
    }
    
    .contact-flex-div2{
        width: 100%;
    }
    #form-div{
        width: 75vw;
    }
    .form-btn-div{
        width: 75vw;
    }
    .contact-logo{
        width: 70vw;
        height: 30vw;
    }
    .contact-SocialMedia{
        display: flex;
        margin-top: 20px;
        justify-content: space-between;
        width: 50vw;
      }
      .SocialMedia {
        display: none;
      }
      .contact-Mail{
        background-image: url("/src/assets/icons8-mail-96.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      
        width: 22px;
        height: 22px;
      
        transition: background-image 0.3s ease, transform 0.3s ease;
      }
      
      .contact-GitHub{
        background-image: url("/src/assets/icons8-github-90.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      
        width: 25px;
        height: 25px;
        transition: background-image 0.3s ease, transform 0.3s ease;
      }
      
      .contact-Linkedin{
        background-image: url("/src/assets/icons8-linkedin-90.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      
        width: 22px;
        height: 22px;
        
        transition: background-image 0.3s ease, transform 0.3s ease;
      }
}
